  /*LOADER*/
  /* Stile per il caricatore */
  .loader {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom right, #212529, #0a0a0a);
    z-index: 1050;
    opacity: 1;
    transition: opacity 0.5s ease;
  }
  
  /* Logo pulsante */
  .logo {
    width: 250px;
    animation: pulse 1.5s infinite;
  }
  
  /* Effetto pulsante */
  
  
  @keyframes pulse {
    0% {
      transform: scale(1);
      opacity: 1;
    }
    50% {
      transform: scale(1.1);
      opacity: 1;
    }
    100% {
      transform: scale(1);
      opacity: 1;
    }
  }
  
  /* Nasconde il caricatore dopo il fade-out */
  .loader.fade-out {
    opacity: 0;
    pointer-events: none;
  }